import React from 'react'
import './MenuComponent.css'

export default class MenuComponent extends React.Component {

    changeState = () => {
        this.props.handleClick(this.props.reference)
    }

    render () {
    return (
        <div className='MenuComponent' >
            <button className= {`MenuText_${this.props.selected === this.props.reference}`} onClick={this.changeState}>{this.props.reference}</button>
        </div>
    )}
}


