import React, { Component } from 'react'
import './Chores.css'
import uuidv4 from 'uuid/v4'
import DropDown from './DropDown'

export default class Chores extends Component {
    constructor(props) {
        super(props)
        const day = new Date()
        const today = new Intl.DateTimeFormat('en-US', { weekday: 'long' }).format(day)
        this.state = { dateOfRequest: today }
    }
    
    getChores() {
        const choreList = {
            Sunday: null,
            Monday: {
                Vitor: ["Surface Cleaning"],
                Keegan: ["No Chores!"],
                Isaac: ["No Chores!"]
            },
            Tuesday: {
                Vitor: ["Trash"],
                Isaac: ["Clean Kitchen", "Bathroom"],
                Keegan: ["No Chores!"]
            },
            Wednesday: {
                Vitor: ["House Task Discussion"],
                Isaac: ["House Task Discussion"],
                Keegan: ["House Task Discussion"]
            },
            Thursday: {
                Vitor: ["No Chores!"],
                Isaac: ["Clean Kitchen"],
                Keegan: ["Surface Cleaning"]
            },
            Friday: null,
            Saturday: {
                Vitor: ["Trash", "Workbench"],
                Isaac: ["Clean Kitchen", "Workbench"],
                Keegan: ["Sweeping/Vaccuming", "Bathroom"]
            },
        }

        const prevRunner = "Vitor" // get from DB
        const runnerOrder = ["Isaac", "Keegan", "Vitor"]
        let newRunner = "";

        for (let i = 0; i < runnerOrder.length; i++) {
            if (runnerOrder[i] === prevRunner) {
                if (i === 2) {
                    newRunner = runnerOrder[0];
                } else {
                    newRunner = runnerOrder[i + 1];
                }
            }
        }

        let today = this.state.dateOfRequest


        if (today === "Saturday") {
        if (newRunner === "Vitor") choreList.Saturday.Vitor.push("Grocery Run")
        if (newRunner === "Isaac") choreList.Saturday.Isaac.push("Grocery Run")
        if (newRunner === "Keegan") choreList.Saturday.Keegan.push("Grocery Run")
        }

        
        return choreList[today]; // !!! change to 'today' const
    }

    handleClickDate = (date) => {
        this.setState({dateOfRequest: date})
    }

    render() {
        const todaysChores = this.getChores()
        if (todaysChores) {
        return (
            <div className="Chores_Content_Wrapper">
                <h1 className={`Title_${this.props.home}`}>The Stream House Chores List</h1>
                <ul className={`List_${this.props.home}`}>Vitor's Chores Today:
                    {todaysChores.Vitor.map(item => {
                        return <li key={uuidv4()}>{item}</li>
                    })}
                </ul>
                <ul className={`List_${this.props.home}`}>Isaac's Chores Today:
                    {todaysChores.Isaac.map(item => {
                        return <li key={uuidv4()}>{item}</li>
                    })}
                </ul>
                <ul className={`List_${this.props.home}`}>Keegan's Chores Today:
                    {todaysChores.Keegan.map(item => {
                        return <li key={uuidv4()}>{item}</li>
                    })}
                </ul>
                <DropDown today={this.state.dateOfRequest} changeDate={this.handleClickDate}/>
            </div>
        )} else {
            return (
                <div className="Chores_Content_Wrapper">
                    <h1 className="Title">No Chores Today!</h1>
                    <DropDown today={this.state.dateOfRequest} changeDate={this.handleClickDate}/>
                </div>
            )
        }
    }
}
