import React, { Component } from 'react'
import './HouseTasks.css'
import TaskItem from './TaskItem'
import uuidv4 from 'uuid/v4'

function Task(title, description, responsible, finished, id) {
        this.title = title // string
        this.description = description // string
        this.responsible = responsible // string
        this.finished = finished // Boolean
        this.id = id //uuidv4
}

const T1 = new Task("Get art for the walls",
"Need art for x y z, preferably xyz if we can", "Vitor", false, uuidv4())

export default class HouseTasks extends Component {
    constructor(props) {
        super(props)

        this.state = { 
            tasks: [],
            addTask: false
         } 
         // get from database
         this.titleRef = React.createRef();
         this.descRef = React.createRef();
         this.respRef = React.createRef();
    }

    componentDidMount() {
        fetch('https://s-h-m-s.herokuapp.com/house-tasks/tasks/')
        .then(response => {
            if (response.ok) {
                return response.json()
            } else {
                return {
                    tasks: []
                }
            }
        }).then(tasks => {
            this.setState({tasks: tasks.tasks})
        })
    }

    storeNewTask(Task) {
        const fetchOptions = {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
              },
            body: JSON.stringify({ tasks: Task })
        }
        fetch('https://s-h-m-s.herokuapp.com/house-tasks/tasks/', fetchOptions)
    }

    handleTaskComplete = (id) => {
        console.log(this.state)
        let newArray = [...this.state.tasks]
        for(let i = 0; i < newArray.length; i++) {
            if (id === newArray[i].id) {
                console.log(id)
                newArray[i].finished = !newArray[i].finished
                this.setState({ tasks: newArray })
                return
            }
        }
    }

    showTaskMenu = () => {
        this.setState({ addTask: true })
    }

    closeTaskMenu = () => {
        this.setState({ addTask: false })
    }

    addTask = () => {
        // save to mongoDB here
        const title = this.titleRef.current.value
        const description = this.descRef.current.value
        const responsible = this.respRef.current.value
        const prevTasks = this.state.tasks

        const task = new Task(title, description, responsible, false, uuidv4())
        this.setState({tasks: [...prevTasks, task]})
        this.storeNewTask([...prevTasks, task])
        this.closeTaskMenu()
    }

    clearFinishedTasks = () => {
        const prevTasks = this.state.tasks
        let newTasks = [];
        for (let i = 0; i < prevTasks.length; i++) {
            if (!prevTasks[i].finished) {
                newTasks.push(prevTasks[i])
                console.log(prevTasks[i])
            }
        }
        this.setState({ tasks: newTasks })
        this.storeNewTask(newTasks)
     }

    render() {
         if (!this.state.addTask) {
        return (
            <div className='House_Tasks_Wrapper'>
                <h1 className="Title">Stream House Tasks</h1>
                {this.state.tasks.map(task => {
                    return <TaskItem handleTaskComplete={this.handleTaskComplete} key={task.id} task={task} />
                })}
                <div className="content_bottom">
                    <button onClick={this.showTaskMenu} className="AddTask_Button">Add a new task?</button>
                    <button onClick={this.clearFinishedTasks} className="FinishedTasks_Button">Clear Finished Tasks</button>
                </div>
            </div>
        )
            } else {
                return (
                    <>
                    <div className='House_Tasks_Wrapper'>
                        <h1 className="Title">Stream House Tasks</h1>
                        {this.state.tasks.map(task => {
                            return <TaskItem handleTaskComplete={this.handleTaskComplete} key={task.id} task={task} />
                        })}
                        <div className="content_bottom">
                            <button onClick={this.showTaskMenu} className="AddTask_Button">Add a new task?</button>
                            <button onClick={this.clearFinishedTasks} className="FinishedTasks_Button">Clear Finished Tasks</button>
                        </div>
                    </div>
                        <div className="Underlay">
                        </div>
                        <div className="AddTask_Wrapper">
                            <p className="Add_Task_Titles">Task Title</p>
                            <input type="text" ref={this.titleRef} className="Title_input" placeholder="Add a title"></input>
                            <p className="Add_Task_Titles">Description for task</p>
                            <input type="text" ref={this.descRef} className="Desc_input" placeholder="Add a description"></input>
                            <p className="Add_Task_Titles">Who should finish this task? This should be considered in a discussion</p>
                            <input type="text" ref={this.respRef} className="Resp_input" placeholder="Who should finish this?"></input>
                                <button className="CloseMenu_button" onClick={this.closeTaskMenu}>X</button>
                                <button className="Add_Task_button" onClick={this.addTask}>Add Task</button>
                        </div>
                    </>
                )
            }
    }
}
