import React from 'react'
import MenuComponent from './MenuComponent'
import './Menu.css'

export default class Menu extends React.Component {



    render () {
    return (
        <div className="Menu">
            <MenuComponent reference="Home" selected={this.props.selected} handleClick={this.props.handleClick} />
            <MenuComponent reference="Shopping List" selected={this.props.selected} handleClick={this.props.handleClick}/>
            <MenuComponent reference="House Tasks" selected={this.props.selected} handleClick={this.props.handleClick}/>
            <MenuComponent reference="Chores" selected={this.props.selected} handleClick={this.props.handleClick}/>
            <MenuComponent reference="About" selected={this.props.selected} handleClick={this.props.handleClick}/>
        </div>
    )}
}
