import Menu from './nav/Menu'
import './App.css'
import Content from './Content/Content.js'
import React, { Component } from 'react'

export default class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
        selected: 'Home'
    }


}

handleClick = (reference) => {
    this.setState({ selected: reference })
}

  render() {
    return (
      <div className="App">
        <div className="Menu">
        <Menu selected={this.state.selected} handleClick={this.handleClick} />
        </div>
        <div className="Content">
        <Content selected={this.state.selected} handleClick={this.handleClick}/>
        </div>
      </div>
    )
  }
}






