import React, { Component } from 'react'
import ListItem from './ListItem'
import './Shopping.css'
import uuidv4 from 'uuid/v4'
import UI from './UI'

export default class Shopping extends Component {
    constructor(props) {
        super(props);

        this.state = { 
            itemList: [],
         } // Hook this up to a database
    }

    // fetch setup

    addItems = (allItems) => {
        const fetchOptions = {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
              },
            body: JSON.stringify({ items: allItems })
        }
        fetch('https://s-h-m-s.herokuapp.com/shopping/items/', fetchOptions)
    }
    
    fetchItems = () => {
        fetch('https://s-h-m-s.herokuapp.com/shopping/items/')
        .then((response) => response.json())
        .then(items => {
            this.setState({itemList: items})
        })
    } 

    componentDidMount() {
        fetch('https://s-h-m-s.herokuapp.com/shopping/items/')
        .then((response) => {
        if (response.ok) { 
            return response.json()
            } else {
                return {
                    items: []
                }
            }
        })
        .then(items => {
            this.setState({itemList: items.items})
        })
        console.log('component mounted')
    }



    handleClickClear = () => {
        let newArray = [...this.state.itemList]
        newArray = newArray.filter(item => !item.done)
        this.addItems(newArray)
        this.setState({ itemList: newArray })
    }

    handleCross = (id) => {
        let newArray = [...this.state.itemList];
        for (let i = 0; i < newArray.length; i++) {
            if (newArray[i].key === id) {
                newArray[i].done = !newArray[i].done 
            }
        }
        this.setState({ itemList: newArray })
        this.addItems(newArray)
    }

    handleAddItem = (item) => {
        let newArray = [...this.state.itemList, { name: item, done:false, key:uuidv4() }]
        this.addItems(newArray)
        this.setState({ itemList: newArray })
    }



    render() {
        if (this.state.itemList === undefined || this.state.itemList.length === 0) {
            return (
                <div className="Shopping_Content_Wrapper">
                    <h1 className="Title"> The Stream House Shopping List is Empty! Add an Item?</h1>
                    <div className="Content_bottom">
                    <UI handleClickClear={this.handleClickClear} handleAddItem={this.handleAddItem} />
                    </div>
                </div>
            )
        } else {
            return (
                <div className="Shopping_Content_Wrapper">
                    <h1 className="Title"> The Stream House Shopping List </h1>
                        <div className="Content_bottom">     
                            {this.state.itemList.map(item => {
                                return <ListItem name={item.name} done={item.done} key={item.key} id={item.key} handleCross={this.handleCross}/>
                            })}
                            <UI handleClickClear={this.handleClickClear} handleClick={this.props.handleClick} handleAddItem={this.handleAddItem} home={this.props.home} />
                        </div>
                </div>
            )
         }
    }
}
