import React, { Component } from 'react'
import './TaskItem.css'

export default class TaskItem extends Component {

    completeTask = () => {
        this.props.handleTaskComplete(this.props.task.id)
    }

    render() {
        return (
            <div className="TaskItem_Wrapper">
                <h3>{this.props.task.title}</h3>
                <p>{this.props.task.description}</p>
                <div className="Input_div">
                <p>Member responsible for this task: {this.props.task.responsible}<input type="checkbox" className="input_task" checked={this.props.task.finished} onChange={this.completeTask}></input></p>
                </div>
            </div>
        )
    }
}
