import React, { Component } from 'react'
import Home from './Home/Home'
import Shopping from './Shopping/Shopping'
import HouseTasks from './HouseTasks/HouseTasks'
import About from './About/About'
import Chores from './Chores/Chores'


export default class Content extends Component {


    render() {
        if (this.props.selected === "Home") return <Home handleClick={this.props.handleClick} />;
        if (this.props.selected === "Shopping List") return <Shopping home={false} />;
        if (this.props.selected === "House Tasks") return <HouseTasks />;
        if (this.props.selected === "About") return <About />;
        if (this.props.selected === "Chores") return <Chores home={false}/>;
    }
}

