import React, { Component } from 'react'
import './DropDown.css'

export default class DropDown extends Component {   
    constructor() {
        super()

        this.state = {
            showMenu: false
        }

        this.showMenu.bind(this)
    }

    showMenu = () => {
        this.setState({showMenu: !this.state.showMenu})
    }



    changeDate = (date) => {
        this.showMenu()
        this.props.changeDate(date)
    }

    render() {
        if (!this.state.showMenu) {
        return (
            <div>   
                <button className="Menu_Display" onClick={this.showMenu}>{this.props.today}</button>
            </div>
        )
        } else {
            return (
            <div>   
                <button className="Menu_Display" onClick={this.showMenu}>{this.props.today}</button>

                <div className="Menu_DD">
                    <button className="Menu_Item" onClick={() => this.changeDate("Sunday")}>Sunday</button>
                    <button className="Menu_Item" onClick={() => this.changeDate("Monday")}>Monday</button>
                    <button className="Menu_Item" onClick={() => this.changeDate("Tuesday")}>Tuesday</button>
                    <button className="Menu_Item" onClick={() => this.changeDate("Wednesday")}>Wednesday</button>
                    <button className="Menu_Item" onClick={() => this.changeDate("Thursday")}>Thursday</button>
                    <button className="Menu_Item" onClick={() => this.changeDate("Friday")}>Friday</button>
                    <button className="Menu_Item" onClick={() => this.changeDate("Saturday")}>Saturday</button>
                </div>
            </div>
            )
        }
    }
}
