import React, { Component } from 'react'
import './ListItem.css'

export default class ListItem extends Component {
    changeState = () => {
        this.props.handleCross(this.props.id)
    }

    render() {
        return (
             <p className={`ListItem_${this.props.done}`}  onClick={this.changeState}><span>&#8226;</span>  {this.props.name}</p>
        )
    }
}
