import React, { Component } from 'react'
import './UI.css'

export default class UI extends Component {
    constructor(props) {
        super(props);

        this.state = { value: "" }
    }

    removeItems = () => {
        this.props.handleClickClear()
    }

    addItem = (e) => {
        if (e.key !== 'Enter') return 
        if (!this.state.value) return
        this.props.handleAddItem(this.state.value)
        this.setState({ value: "" })
    }

    handleChange = (e) => {
        this.setState({ value: e.target.value })
    }

    handleClick = () => {
        this.props.handleClick('Shopping List')
    }

    render() {
        if (this.props.home) {
            return (
                <div className="UIWrapper">
                    <button className="UIButton" onClick={this.removeItems}>Clear finished shopping list items</button>
                    <input placeholder="Add an Item" className={"UIInput"} onKeyDown={this.addItem} onChange={this.handleChange} type="text" value={this.state.value}></input>
                    <button className="ShoppingNav" onClick={this.handleClick}>See all items</button>
                </div>
            )
        } else {
            return (
            <div className="UIWrapper">
                <button className="UIButton" onClick={this.removeItems}>Clear finished shopping list items</button>
                <input placeholder="Add an Item" className={"UIInput"} onKeyDown={this.addItem} onChange={this.handleChange} type="text" value={this.state.value}></input>
            </div>
            )
        }
    }
}
