import React, { Component } from 'react'
import './About.css'

export default class About extends Component {
    render() {
        return (
            <div className="About_Content_Wrapper">
                    <h1 className="Title_About">About</h1>
                    <p>Thank you for using the Stream House Management Service! (SHMS)</p>
                    <p>This is a small project being developed in order to learn and experiment using the React framework.</p>
                    <p>Many CSS techniques were tested during the development of this app, with some more succesful than others.</p>
                    <p>SHMS serves a practical purpose within the 'Stream House' as it allows our members to stay on top of their chores and house tasks, as well as
                        allowing members to share a communal shopping list, all hosted on AWS using MongoDB and routed with Express.js.
                    </p>
                    <p>This app is under development and is subject to change. Thank you for reading!</p>
                    <p className="Signature">-Vitor</p>
            </div>
        )
    }
}
